const getAllProducts = () => trousersMaleProducts;

const getProducts = (count) => {
  const max = trousersMaleProducts.length - count;
  const min = 0;
  const start = Math.floor(Math.random() * (max - min) + min);

  return trousersMaleProducts.slice(start, start + count);
};

const getProductBySlug = (slug) =>
  trousersMaleProducts.find((e) => e.slug === slug);

const trouserProductData = {
  getAllProducts,
  getProducts,
  getProductBySlug,
};

export default trouserProductData;
