const getAllProducts = () => maleProducts;

const getProducts = (count) => {
  const max = maleProducts.length - count;
  const min = 0;
  const start = Math.floor(Math.random() * (max - min) + min);

  return maleProducts.slice(start, start + count);
};

const getProductBySlug = (slug) => maleProducts.find((e) => e.slug === slug);

const maleProductDat = {
  getAllProducts,
  getProducts,
  getProductBySlug,
};

export default maleProductDat;
