import React from "react";
import { Link } from "react-router-dom";
import Grid from "./Grid";

const linkSearchMost = [
  {
    display: "o mt chim",
    path: "/ao-mat-chim",
  },
  {
    display: "o phng n",
    path: "/ao-phong-nu",
  },
  {
    display: "o phng nam",
    path: "/ao-phong-nam",
  },
  {
    display: "Jean n",
    path: "/jeam-nu",
  },
  {
    display: "chn vy",
    path: "/ao-mat-chim",
  },
  {
    display: "S mi n",
    path: "/so-mi-nu",
  },
  {
    display: "S mi nam",
    path: "/so-mi-nam",
  },
  {
    display: "o polo n cafe",
    path: "/ao-polo-nu-cafe",
  },
];
const SearchBest = () => {
  return (
    <div className="container">
      <div className="search-best">
        <div className="search-best__title">tm kim nhiu nht</div>
        <div className="search-best__content">
          <Grid col={4} mdCol={2} smCol={2} gap={10}>
            {linkSearchMost.map((item, index) => (
              <div className="search-best__content__item" key={index}>
                <Link to={item.path}>{item.display}</Link>
              </div>
            ))}
          </Grid>
        </div>
      </div>
    </div>
  );
};

export default SearchBest;
