const getAllProducts = () => dressmFemaleProducts;

const getProducts = (count) => {
  const max = dressmFemaleProducts.length - count;
  const min = 0;
  const start = Math.floor(Math.random() * (max - min) + min);

  return dressmFemaleProducts.slice(start, start + count);
};

const getProductBySlug = (slug) =>
  dressmFemaleProducts.find((e) => e.slug === slug);

const dressProductData = {
  getAllProducts,
  getProducts,
  getProductBySlug,
};

export default dressProductData;
